import React from 'react'
import BigNumber from 'bignumber.js'
import { useTokenContract } from './useContract'
import { useSingleCallResult } from '../state/multicall/hooks'

const cakeAddress = "0x95336aC5f7E840e7716781313e1607F7C9D6BE25"

export const useMaxTransferLimitAmount = () => {
  const contract = useTokenContract(cakeAddress, false)
  const maxTransferLimitAmount: BigNumber = useSingleCallResult(contract, 'maxTransferLimitAmount')?.result?.[0]
  return maxTransferLimitAmount
}

export const useSellTax = () => {
  const contract = useTokenContract(cakeAddress, false)
  console.log(contract)
  const sellTax: BigNumber = useSingleCallResult(contract, 'transferTaxRateSell')?.result?.[0]
  return sellTax
}

export const useBuyTax = () => {
  const contract = useTokenContract(cakeAddress, false)
  const buyTax: BigNumber = useSingleCallResult(contract, 'transferTaxRateBuy')?.result?.[0]
  return buyTax
}
